import React, {useEffect, useState} from "react";
import {Transition} from "react-transition-group";
import config from "../../../../../../config";
import style from './index.module.css'
import Story from "./components/story";
import Module from '../../../modules/modules'

// TODO: попробовать использовать не Img, а Div-блоки с фоновыми кусками изображения [UPD: отклонено / DIV не содержат onLoad обработчика + коробят изображение по соотношению]

let Sound = {
    audio: new Audio() || false,
    settings: {
        defaultMuted: false,
        defaultVolume: .2,
        defaultLoop: true
    },
    set: function (url) {
        url = ''+url
        // const check = await isUrlFound(url)
        // if (!check) return
        if (this.audio.src==="" || this.audio.src===null) {
            this.audio.src = url
            this.audio.volume = this.settings.defaultVolume
            this.settings = {
                ...this.settings,
                volume: this.settings.defaultVolume
            }
            this.audio.loop = this.settings.defaultLoop
            this.audio.muted = this.settings.defaultMuted
            this.audio.play().catch(err => console.log(err))
        } else if (this.audio.src!==url) {
            this.update(url).catch()
        }
    },
    update: async function (url) {
        const delay = (ms) => { return new Promise(resolve => setTimeout(resolve, ms)); }
        const timer = 600
        const volumeDelta = this.audio.volume/(timer*.5)

        for (let i = timer*.5-1; i >= 0; i--) {
            await delay(1)
            let vol = this.audio.volume - volumeDelta
            this.audio.volume = vol<0 ? 0 : vol
        }
        this.audio.pause()
        this.audio.src = url
        this.audio.play().catch()
        for (let i = 0; i <= timer*.5-1; i++) {
            await delay(1)
            let vol = this.audio.volume + volumeDelta
            this.audio.volume = vol<=this.settings.volume ? vol : this.settings.volume
        }
    },
    change: {
        muted: function (val) {
            Sound.settings.muted = val
            Sound.audio.muted = val
        },
        volume: function (val) {
            Sound.settings.volume = val*.01
            Sound.audio.volume = val*.01
            if (val===0) {
                this.muted(true)
            } else if (Sound.settings.muted) {
                this.muted(false)
            }
            console.log(Sound.settings)
        }
    }
}
let Video = {
    video: null,
    settings: {
        autoPlay: true,
        muted: true,
        controls: false,
        loop: true
    },
    set: function (video) {
        return (
            <video
                muted={video.muted || this.settings.muted}
                autoPlay={video.autoPlay || this.settings.autoPlay}
                controls={video.controls || this.settings.controls}
                loop={video.loop || this.settings.loop}
            >
                <source src={video.src} type={`video/${video.format || 'mp4'}`}/>
            </video>
        )
    }
}

const Index = (props) => {
    const [userWay, setUserWay] = useState([0])
    const [current, setCurrent] = useState({})
    const [content, setContent] = useState(null)
    const [card, setCard] = useState(null)

    const setAnswers = (lastStep) => {
        // - Кнопки
        if (Story[lastStep].answers.buttons) {
            const element = Story[lastStep].answers.buttons.map((button,i) =>
                <button onClick={() => setUserWay([...userWay, button.target])} key={i}>{button.text}</button>
            )
            setCard(element)
        }
        // - Ввод
        if (Story[lastStep].answers.input) {
            const answers = Story[lastStep].answers.input
            const Check = (text) => {
                let elements = [<input type={'text'} onChange={(e) => Check(e.target.value)} autoFocus={true} defaultValue={text}/>]
                answers.forEach(answer => {
                    if (answer.answers.includes(text.toLowerCase())) {
                        /*<input type={'text'} onChange={(e) => Check(e.target.value)} autoFocus={true}/>*/
                        elements = [...elements, <button onClick={() => setUserWay([...userWay, answer.target])}>{answer.text}</button>]
                    }
                })

                if (element!==undefined) {
                    setCard(<>{elements.map(el => el)}</>)
                }
            }
            const element = <><input type={'text'} onChange={(e) => Check(e.target.value)} autoFocus={true}/></>
            setCard(element)
        }
    }
    const updateStoryModule = (Story, lastStep) => {
        Story[lastStep].content.module = {
            ...Story[lastStep].content.module,
            settings: {
                ...Story[lastStep].content.module.settings,
                isFinished: true
            }
        }
    }

    useEffect(() => {
        // Reset
        setContent(null)

        // Const
        const lastStep = userWay[userWay.length-1]

        // Стили
        if (Story[lastStep].style && Story[lastStep].style.backgroundImage) {
            setCurrent({
                ...current,
                style: {
                    ...current.style,
                    backgroundImage: `url("${Story[lastStep].style.backgroundImage}")`
                }
            })
        }

        // Контент
        if (Story[lastStep].content) {
            if (Story[lastStep].content.module) {
                // - Модули
                const Reaction = () => {
                    updateStoryModule(Story, lastStep)
                    setAnswers(lastStep)
                }
                const settings = {
                    isFinished: false,
                    ...Story[lastStep].content.module.settings,
                    callback: Reaction
                }
                setContent(<Module name={Story[lastStep].content.module.name} settings={settings}/>)
            } else if (Story[lastStep].content.media) {
                // - Медиа
                // - - Изображение
                if (Story[lastStep].content.media && Story[lastStep].content.media.image) {
                    if (Story[lastStep].content.media.image.src && Story[lastStep].content.media.image.src !== null) {
                        setContent(<div className={style.wrapper}><img src={Story[lastStep].content.media.image.src}
                                                                       alt={'Изображение не прогружено'}
                                                                       title={Story[lastStep].content.media.image.name ? Story[lastStep].content.media.image.name : null}/>
                        </div>)
                    }
                }
                // - - Аудио
                if (Story[lastStep].content.media && Story[lastStep].content.media.audio) {
                    if (Story[lastStep].content.media.audio.src && Story[lastStep].content.media.audio.src !== null) {
                        Sound.set(Story[lastStep].content.media.audio.src)
                    }
                }
                // - - Видео
                if (Story[lastStep].content.media && Story[lastStep].content.media.video) {
                    if (Story[lastStep].content.media.video.src && Story[lastStep].content.media.video.src !== null) {
                        setContent(Video.set(Story[lastStep].content.media.video))
                    }
                }
            }
        }

        // Карта ответов
        if (Story[lastStep].answers) {
            if (Story[lastStep].content && Story[lastStep].content.module) {
                setCard(null)
            } else {
                setAnswers(lastStep)
            }
        }

    }, [userWay])

    const duration = 1500
    const defaultStyle = {
        transition: `opacity ${duration}ms ease-in-out`,
        opacity: 0,
    }
    const transitionStyles = {
        entering: { opacity: 1 },
        entered:  { opacity: 1 },
        exiting:  { opacity: 0 },
        exited:  { opacity: 0 },
    };

    return(
        <main style={{backgroundImage:current.style && current.style.backgroundImage && current.style.backgroundImage}}>

            <Transition in={content} timeout={duration}>
                {state => (
                    <div className={style.content} style={{
                        ...defaultStyle,
                        ...transitionStyles[state]
                    }}>
                            {content && content}
                </div>
                )}
            </Transition>
            <div className={style.cardsList} style={{position:'relative'}}>
                {Story[userWay[userWay.length - 1]].title &&
                    <h3>{Story[userWay[userWay.length - 1]].title}</h3>
                }
                {Story[userWay[userWay.length - 1]].text &&
                    <p>{Story[userWay[userWay.length - 1]].text}</p>
                }
                <div className={style.answersList}>
                    {/*{Story[userWay[userWay.length-1]].answers && Story[userWay[userWay.length-1]].answers.map((button,i) =>*/}
                    {/*    <button onClick={() => {setUserWay([...userWay, button.target])}} key={i}>{button.text}</button>*/}
                    {/*)}*/}
                    {card}
                    {userWay.length > 1 &&
                        <button onClick={() => {setUserWay(userWay.slice(0, userWay.length - 1))}}>Вернуться</button>
                    }
                </div>
                <div className={style.controls}>
                    <a href={`${config.repo}/`}>Домой</a>
                    <div className={style.sound}>
                        <button
                            onClick={(e) => {
                                const val = Sound.settings.muted ? false : true
                                Sound.change.muted(val)
                            }}
                        >[no.sound/sound]</button>
                        <input
                            type={'range'}
                            min={0}
                            max={100}
                            defaultValue={Sound.settings.defaultVolume*100}
                            title={Sound.settings.muted ? 'Без звука' : `${Sound.settings.volume*100}/100`}
                            onChange={(e) => {
                                Sound.change.volume(e.target.value)
                                e.target.title = Sound.settings.muted ? 'Без звука' : `${Sound.settings.volume*100}/100`
                            }}
                        />
                    </div>
                </div>
            </div>
        </main>
    )
}

export default Index